___CSS_LOADER_EXPORT___.push([module.id, ".preview[data-v-11e13102]{max-width:720px}", "",{"version":3,"sources":["webpack://./resources/js/frontend/components/social/ArticlePreview.vue"],"names":[],"mappings":"AAwrBA,0BACA,eACA","sourcesContent":["<template>\n    <div class=\"preview align-self-center\">\n        <h1 class=\"text-center\">效果預覽</h1>\n        <canvas class=\"rounded mx-auto d-block w-100\" ref=\"canvasView\">\n            <!-- 倘若使用者的瀏覽器並不支援 canvas，將會顯示該段內容。 -->\n            您的瀏覽器必須支援 HTML5 標籤語法，才能使用圖片(即時)預覽功能。\n        </canvas>\n        <p class=\"text-danger\">因為字型檔(font)容量很大，所以我們決定把字型的預覽繪製當作有跟沒有之間。</p>\n    </div>\n</template>\n\n<script>\nexport default {\n    name: \"ArticlePreview\",\n    props: {\n        /**\n         * 文字內容\n         */\n        content: {\n            type: String,\n            required: true,\n        },\n        /**\n         * 主題\n         */\n        theme: {\n            type: String,\n            required: true,\n        },\n        /**\n         * 字型\n         */\n        font: {\n            type: String,\n            required: true,\n        },\n    },\n    data() {\n        return {\n            /**\n             * 畫布設定\n             */\n            canvas: {\n                view: null,\n                ctx: null,\n                width: 960,\n                height: 720,\n                center: false,\n            },\n            /**\n             * 預設值\n             */\n            default: {\n                canvas: {\n                    width: 960,\n                    height: 720,\n                },\n                line: {\n                    height: 80,\n                },\n            },\n            /**\n             * 主題選項\n             */\n            themes: {\n                'black-green': {\n                    background: '#000000',\n                    text: '#00FF3B',\n                },\n                'black-yellow': {\n                    background: '#000000',\n                    text: '#EBD443',\n                },\n                'black-white': {\n                    background: '#000000',\n                    text: '#F8F9FA',\n                },\n                'black-red': {\n                    background: '#000000',\n                    text: '#DC3545',\n                },\n                'sweet-panda': {\n                    background: '#E83E8C',\n                    text: '#F8F9FA',\n                },\n                'blue-white': {\n                    background: '#007BFF',\n                    text: '#F8F9FA',\n                },\n                'white-blue': {\n                    background: '#F8F9FA',\n                    text: '#007BFF',\n                },\n                'laravel': {\n                    background: '#F4645F',\n                    text: '#F8F9FA',\n                },\n                'soft-green': {\n                    background: '#39C5BB',\n                    text: '#000000',\n                },\n                'grey-pikachu': {\n                    background: '#2F3437',\n                    text: '#FFD547',\n                },\n                'grey-eevee': {\n                    background: '#2F3437',\n                    text: '#E7AF56',\n                },\n                'pikachu-grey': {\n                    background: '#FFD547',\n                    text: '#2F3437',\n                },\n                'eevee-grey': {\n                    background: '#E7AF56',\n                    text: '#2F3437',\n                },\n                'chinese-new-year': {\n                    background: '#A61723',\n                    text: '#D8B06A',\n                },\n                'reverse-chinese-new-year': {\n                    background: '#D8B06A',\n                    text: '#A61723',\n                },\n                'devotion': {\n                    background: '#F11541',\n                    text: '#000000',\n                },\n                'windows-10-error': {\n                    background: '#007BD0',\n                    text: '#F8F9FA',\n                },\n                'windows-10-error-testing': {\n                    background: '#107C10',\n                    text: '#F8F9FA',\n                },\n                'pink': {\n                    background: '#F8C0C8',\n                    text: '#FF5376',\n                },\n                'broken-think': {\n                    background: '#F8F9FA',\n                    text: '#000000',\n                },\n                'furry-broken-think': {\n                    background: '#F8F9FA',\n                    text: '#000000',\n                },\n            },\n            /**\n             * 字型選項\n             */\n            fonts: {\n                'auraka': {\n                    name: 'AURAKA 點陣宋字型',\n                    font: 'Auraka',\n                },\n                'kc24m': {\n                    name: '國喬點陣字型',\n                    font: 'KC24M',\n                },\n                'microsoft-jheng-hei': {\n                    name: '微軟正黑體',\n                    font: 'Microsoft JhengHei',\n                },\n                'mingliu': {\n                    name: '新細明體',\n                    font: 'Mingliu',\n                },\n                'kaiu': {\n                    name: '標楷體',\n                    font: 'Kaiu',\n                },\n                'fot-matissepro-eb': {\n                    name: '極粗明朝體',\n                    font: 'MatissePro EB',\n                },\n                'taipei-sans-tc-beta-bold': {\n                    name: '台北黑體',\n                    font: 'Taipei Sans TC Beta',\n                },\n            },\n        };\n    },\n    mounted() {\n        this.resetCanvasView();\n        this.settingCanvasViewSize();\n        this.drawingBackground();\n        this.drawingBackgroundImage();\n        this.drawingLogo();\n        this.drawingUrl();\n        this.drawingContent();\n    },\n    methods: {\n        /**\n         * 重置 Canvas view 物件、ctx 物件\n         *\n         * @return void\n         */\n        resetCanvasView() {\n            this.canvas.view = this.$refs.canvasView;\n            this.canvas.ctx = this.canvas.view.getContext(\"2d\");\n        },\n        /**\n         * 設定 Canvas view 的寬度(width)、高度(height)\n         *\n         * @return void\n         */\n        settingCanvasViewSize() {\n            /**\n             * 獲得文章內容經過分割後的行數\n             */\n            let lineCount = this.contentSplit().length;\n            /**\n             * 判斷文章是否垂直置中，假設每行佔 ${default.line.height} 的高度，文章內容小於 600px 則會垂直置中對齊\n             */\n            let canvasViewCenter = (lineCount * this.default.line.height) < 600 ? true : false;\n            /**\n             * 計算出 Canvas 的寬度(width)、高度(height)\n             * 如果文章沒有很長，那就給予預設高度 ${default.canvas.height}\n             * 如果文章過長，就根據其他元件的高度 + (每行 * ${default.line.height})\n             */\n            let canvasViewWidth = this.default.canvas.width;\n            let canvasViewHeight = canvasViewCenter ? this.default.canvas.height : 144 + (lineCount * this.default.line.height);\n            /**\n             * 如果是選擇特殊樣式，會有其它裝飾元素，必須再額外給予更多的寬度(width)、高度(height)\n             */\n            switch (this.theme) {\n                // 主題: Windows 10 錯誤畫面\n                case 'windows-10-error':\n                    canvasViewHeight += 360;\n                    break;\n\n                // 主題: Windows 10 測試人員計畫 錯誤畫面\n                case 'windows-10-error-testing':\n                    canvasViewHeight += 360;\n                    break;\n\n                // 主題: 支離滅裂な思考・発言\n                case 'broken-think':\n                    canvasViewHeight += 160;\n                    canvasViewWidth += 349;\n                    break;\n\n                // 主題: 不獣控制な思考・発言\n                case 'furry-broken-think':\n                    canvasViewHeight += 160;\n                    canvasViewWidth += 349;\n                    break;\n            }\n            /**\n             * 將計算結果寫入 data() 當中\n             */\n            this.canvas.width = canvasViewWidth;\n            this.canvas.height = canvasViewHeight;\n            this.canvas.center = canvasViewCenter;\n            /**\n             * 將 canvas 物件重新設定其寬度(width)、高度(height)\n             */\n            this.canvas.view.width = this.canvas.width;\n            this.canvas.view.height = this.canvas.height;\n        },\n        /**\n         * 對畫布繪製背景顏色\n         *\n         * @return void\n         */\n        drawingBackground() {\n            this.canvas.ctx.fillStyle = this.themes[this.theme].background;\n            this.canvas.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);\n        },\n        /**\n         * 對畫布繪製背景圖片\n         *\n         * @return void\n         */\n        drawingBackgroundImage() {\n            let self = this;\n            switch (this.theme) {\n                // 主題: 慈孤觀音\n                case 'devotion':\n                    var sources = {\n                        devotion: '/img/frontend/cards/devotion-bg.png',\n                    };\n                    this.loadingImages(sources, function (images) {\n                        self.canvas.ctx.drawImage(images.devotion, 360, 64);\n                        self.drawingLogo();\n                        self.drawingUrl();\n                    });\n                    break;\n\n                // 主題: Windows 10 錯誤畫面\n                case 'windows-10-error':\n                    var sources = {\n                        qrcode: '/img/frontend/cards/qrcode.png',\n                    };\n                    this.loadingImages(sources, function (images) {\n                        self.canvas.ctx.drawImage(images.qrcode, 24, self.canvas.height - 204);\n                        self.drawingLogo();\n                        self.drawingUrl();\n                    });\n                    break;\n\n                // 主題: Windows 10 測試人員計畫 錯誤畫面\n                case 'windows-10-error-testing':\n                    var sources = {\n                        qrcode: '/img/frontend/cards/qrcode.png',\n                    };\n                    this.loadingImages(sources, function (images) {\n                        self.canvas.ctx.drawImage(images.qrcode, 24, self.canvas.height - 204);\n                        self.drawingLogo();\n                        self.drawingUrl();\n                    });\n                    break;\n\n                // 主題: 支離滅裂な思考・発言\n                case 'broken-think':\n                    /**\n                     * 定義圖片資源\n                     * background => 背景的紫色圓圈\n                     * people => 支離破碎的人物\n                     * arrow => 對話框的箭頭\n                     */\n                    var sources = {\n                        background: '/img/frontend/cards/fragmented_background.png',\n                        people: '/img/frontend/cards/fragmented_people.png',\n                        arrow: '/img/frontend/cards/fragmented_background_arrow.png',\n                    };\n                    this.loadingImages(sources, function (images) {\n                        /**\n                         * 繪製背景圓圈與人物\n                         */\n                        self.canvas.ctx.drawImage(images.background, 0, self.canvas.height - 560);\n                        self.canvas.ctx.drawImage(images.people, 36, self.canvas.height - 542);\n\n                        /**\n                         * 繪製對話框\n                         */\n                        self.canvas.ctx.lineJoin = 'round';\n                        self.canvas.ctx.lineWidth = 8;\n                        self.canvas.ctx.strokeRect(353, 40, self.canvas.width - 381, self.canvas.height - 282);\n                        self.canvas.ctx.fillStyle = '#FFFFFF';\n                        self.canvas.ctx.fillRect(357, 44, self.canvas.width - 389, self.canvas.height - 290);\n\n                        /**\n                         * 繪製對話框的箭頭\n                         */\n                        self.canvas.ctx.drawImage(images.arrow, 312, self.canvas.height - 388);\n                        self.drawingLogo();\n                        self.drawingUrl();\n                        self.drawingContent();\n                    });\n                    break;\n\n                // 主題: 不獣控制な思考・発言\n                case 'furry-broken-think':\n                    /**\n                     * 定義圖片資源\n                     * background => 背景的紫色圓圈\n                     * wolf => 支離破碎的狼\n                     * arrow => 對話框的箭頭\n                     */\n                    var sources = {\n                        background: '/img/frontend/cards/fragmented_background.png',\n                        wolf: '/img/frontend/cards/fragmented_wolf.png',\n                        arrow: '/img/frontend/cards/fragmented_background_arrow.png',\n                    };\n                    this.loadingImages(sources, function (images) {\n                        /**\n                         * 繪製背景圓圈與狼\n                         */\n                        self.canvas.ctx.drawImage(images.background, 0, self.canvas.height - 560);\n                        self.canvas.ctx.drawImage(images.wolf, 12, self.canvas.height - 482);\n\n                        /**\n                         * 繪製對話框\n                         */\n                        self.canvas.ctx.lineJoin = 'round';\n                        self.canvas.ctx.lineWidth = 8;\n                        self.canvas.ctx.strokeRect(353, 40, self.canvas.width - 381, self.canvas.height - 282);\n                        self.canvas.ctx.fillStyle = '#FFFFFF';\n                        self.canvas.ctx.fillRect(357, 44, self.canvas.width - 389, self.canvas.height - 290);\n\n                        /**\n                         * 繪製對話框的箭頭\n                         */\n                        self.canvas.ctx.drawImage(images.arrow, 312, self.canvas.height - 388);\n                        self.drawingLogo();\n                        self.drawingUrl();\n                        self.drawingContent();\n                        // self.drawingFeature();\n                    });\n                    break;\n            }\n        },\n        /**\n         * 對畫布繪製 LOGO\n         *\n         * @return void\n         */\n        drawingLogo() {\n            switch (this.theme) {\n                // 主題: Windows 10 錯誤畫面\n                case 'windows-10-error':\n                    this.canvas.ctx.font = '36px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('若要深入了解，您稍候可以線上搜尋此:', 228, this.canvas.height - 160);\n                    this.canvas.ctx.fillText('純靠北工程師 0xINIT_ENGINEER', 228, this.canvas.height - 120);\n                    this.canvas.ctx.fillText('請訪問 https://init.engineer', 228, this.canvas.height - 40);\n                    break;\n\n                // 主題: Windows 10 測試人員計畫 錯誤畫面\n                case 'windows-10-error-testing':\n                    this.canvas.ctx.font = '36px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('若要深入了解，您稍候可以線上搜尋此:', 228, this.canvas.height - 160);\n                    this.canvas.ctx.fillText('純靠北工程師 0xINIT_ENGINEER', 228, this.canvas.height - 120);\n                    this.canvas.ctx.fillText('請訪問 https://init.engineer', 228, this.canvas.height - 40);\n                    break;\n\n                // 主題: 支離滅裂な思考・発言\n                case 'broken-think':\n                    this.canvas.ctx.font = '72px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('支離滅裂な', 360, this.canvas.height - 160);\n                    this.canvas.ctx.fillText('思考・発言', 360, this.canvas.height - 80);\n                    this.canvas.ctx.font = '36px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('純靠北工程師', this.canvas.width - 232, this.canvas.height - 24);\n                    break;\n\n                // 主題: 不獣控制な思考・発言\n                case 'furry-broken-think':\n                    this.canvas.ctx.font = '72px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('不獣控制な', 360, this.canvas.height - 160);\n                    this.canvas.ctx.fillText('思考・発言', 360, this.canvas.height - 80);\n                    this.canvas.ctx.font = '36px ' + this.canvas.font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('純靠北工程師', this.canvas.width - 232, this.canvas.height - 24);\n                    break;\n\n                // 預設，會在右下角印上 LOGO 字樣\n                default:\n                    this.canvas.ctx.font = '36px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('純靠北工程師', this.canvas.width - 232, this.canvas.height - 24);\n                    break;\n            }\n        },\n        /**\n         * 對畫布繪製網站連結\n         *\n         * @return void\n         */\n        drawingUrl() {\n            switch (this.theme) {\n                // 主題: Windows 10 錯誤畫面\n                case 'windows-10-error':\n                    this.canvas.ctx.font = '192px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText(':(', 48, 192);\n                    break;\n\n                // 主題: Windows 10 測試人員計畫 錯誤畫面\n                case 'windows-10-error-testing':\n                    this.canvas.ctx.font = '192px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText(':(', 48, 192);\n                    break;\n\n                // 預設，會在左下角印上網站連結宣傳\n                default:\n                    this.canvas.ctx.font = '36px ' + this.fonts[this.font].font;\n                    this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                    this.canvas.ctx.fillText('發文傳送門 https://init.engineer', 16, this.canvas.height - 24);\n                    break;\n            }\n        },\n        /**\n         * 對畫布繪製文字內容\n         *\n         * @return void\n         */\n        drawingContent() {\n            /**\n             * 先取得整理過後的內容字串 array[string]\n             * 並且透過 forEach 的方式逐一繪製內容\n             */\n            let contentList = this.contentSplit();\n            contentList.forEach(function (contentValue, contentKey) {\n                /**\n                 * 定義 x, y 基準點\n                 */\n                let xPoint = 36;\n                let yPoint = 0;\n\n                /**\n                 * 判斷文章內容是否需要垂直置中\n                 * 垂直置中:\n                 * 向上對齊:\n                 */\n                if (this.canvas.center) {\n                    yPoint = 440 + ((contentKey - 1) * 80 - contentList.length * 40);\n                } else {\n                    yPoint = 96 + contentKey * 80;\n                }\n\n                /**\n                 * 根據主題(theme)去判斷是否需要額外修改 x, y 基準點\n                 */\n                switch (this.theme) {\n                    // 主題: Windows 10 錯誤畫面\n                    case 'windows-10-error':\n                        yPoint += 240;\n                        break;\n                    // 主題: Windows 10 測試人員計畫 錯誤畫面\n                    case 'windows-10-error-testing':\n                        yPoint += 240;\n                        break;\n                    // 主題: 支離滅裂な思考・発言\n                    case 'broken-think':\n                        xPoint += 349;\n                        yPoint += 24;\n                        break;\n                    // 主題: 不獣控制な思考・発言\n                    case 'furry-broken-think':\n                        xPoint += 349;\n                        yPoint += 24;\n                        break;\n                }\n\n                /**\n                 * 根據基準點來去繪製當前行數應該出現的位置\n                 */\n                this.canvas.ctx.font = '63px ' + this.fonts[this.font].font;\n                this.canvas.ctx.fillStyle = this.themes[this.theme].text;\n                this.canvas.ctx.fillText(contentValue, xPoint, yPoint);\n            }.bind(this));\n        },\n\n        // ...\n\n        /**\n         * 字串切割，並且以陣列(array[string])的方式回傳\n         *\n         * @return array[string]\n         */\n        contentSplit() {\n            /**\n             * 先準備一個用來儲存結果的字串陣列\n             */\n            let responseList = [];\n            /**\n             * 將文字內容以換行符號來分割成字串陣列，並且迴圈執行每行內容\n             */\n            let contentList = this.content.split(/\\r\\n|\\r|\\n/);\n            contentList.forEach(function (contentValue) {\n                /**\n                 * 計算當前文字內容長度\n                 */\n                let content_strlen = encodeURIComponent(contentValue).replace(/%[A-F\\d]{2}/g, \"U\").length;\n                if (content_strlen <= 42) {\n                    /**\n                     * 如果內容長度小於或等於 42，直接將本行新增到結果陣列\n                     */\n                    responseList.push(contentValue);\n                } else {\n                    /**\n                     * 如果內容長度大於 42，因為內容太長，需要分割換行\n                     *\n                     * conntentWidth => 長度計數器\n                     * charStrring => 字串暫存\n                     * contentValueList => 內容分割成陣列\n                     */\n                    let contentWidth = 0;\n                    let charString = \"\";\n                    let contentValueList = contentValue.split(\"\");\n                    /**\n                     * 將內容陣列以迴圈的方式來逐一執行\n                     */\n                    contentValueList.forEach(function (charValue, charKey) {\n                        /**\n                         * 判斷當前字元的長度\n                         */\n                        let charStrlen = encodeURIComponent(charValue).replace(/%[A-F\\d]{2}/g, \"U\").length;\n                        /**\n                         * 字元長度 3 位元，那麼紀錄長度為 1，否則為 0.5\n                         * 字元儲存到字串暫存當中\n                         */\n                        contentWidth += charStrlen == 3 ? 1 : 0.5;\n                        charString += charValue;\n                        /**\n                         * 判斷是否還有下一個字元\n                         */\n                        if (charKey + 1 in contentValueList) {\n                            /**\n                             * 字元長度 3 位元，那麼紀錄長度為 1，否則為 0.5\n                             */\n                            let nextCharStrlen = encodeURIComponent(contentValueList[charKey + 1]).replace(/%[A-F\\d]{2}/g, \"U\").length;\n                            let nextCharWidth = nextCharStrlen == 3 ? 1 : 0.5;\n                            /**\n                             * 如當前長度再加上下一個字元的長度，長度大於字元最大限度\n                             */\n                            if (contentWidth + nextCharWidth >= 14) {\n                                /**\n                                 * 將當前的字串暫存做一個斷點，儲存到結果字串陣列\n                                 * 並且清空字串暫存、歸零長度計數器\n                                 */\n                                responseList.push(charString);\n                                contentWidth = 0;\n                                charString = \"\";\n                            }\n                        }\n                    });\n\n                    /**\n                     * 如果剩下的內容不是空的，那麼就把剩下的內容儲存到結果字串陣列\n                     */\n                    if (charString != \"\") {\n                        responseList.push(charString);\n                    }\n                }\n            });\n\n            /**\n             * 字串分割完畢，將結果字串陣列回傳\n             */\n            return responseList;\n        },\n        /**\n         * 動態載入圖片\n         *\n         * @param array[string] sources 需要動態載入的圖片位址\n         * @param function callback 載入後所需要執行的事件\n         *\n         * @return void\n         */\n        loadingImages(sources, callback) {\n            var images = {},\n                loadedImages = 0,\n                numImages = Object.keys(sources).length;\n            for (var src in sources) {\n                images[src] = new Image();\n                images[src].onload = function () {\n                    if (++loadedImages >= numImages) {\n                        callback(images);\n                    }\n                };\n                images[src].src = sources[src];\n            }\n        },\n    },\n    watch: {\n        /**\n         * 內容更新\n         */\n        \"content\": function (val) {\n            this.resetCanvasView();\n            this.settingCanvasViewSize();\n            this.drawingBackground();\n            this.drawingBackgroundImage();\n            this.drawingLogo();\n            this.drawingUrl();\n            this.drawingContent();\n        },\n        /**\n         * 主題更新\n         */\n        \"theme\": function (val) {\n            this.resetCanvasView();\n            this.settingCanvasViewSize();\n            this.drawingBackground();\n            this.drawingBackgroundImage();\n            this.drawingLogo();\n            this.drawingUrl();\n            this.drawingContent();\n        },\n        /**\n         * 字型更新\n         */\n        \"font\": function (val) {\n            this.resetCanvasView();\n            this.settingCanvasViewSize();\n            this.drawingBackground();\n            this.drawingBackgroundImage();\n            this.drawingLogo();\n            this.drawingUrl();\n            this.drawingContent();\n        },\n    },\n};\n</script>\n\n<style scoped>\n    .preview {\n        max-width: 720px;\n    }\n</style>\n"],"sourceRoot":""}]);
