ground-repeat: no-repeat;\n    background-position: center;\n}\n\n/* Rounded sliders */\n.slider.round {\n    border-radius: 12px;\n}\n.slider.round:before {\n    border-radius: 50%;\n}\n</style>\n"],"sourceRoot":""}]);
