___CSS_LOADER_EXPORT___.push([module.id, ".btn[data-v-2f8b5392]{border-radius:4px;color:#fff;cursor:pointer;display:inline-block;height:48px;margin:0;padding:2px;position:relative;width:60px}.btn.yes[data-v-2f8b5392]{background-color:#1abc9c;margin-right:6px}.btn.yes[data-v-2f8b5392]:hover{background-color:#1dc8a6}.btn.no[data-v-2f8b5392]{background-color:#d8495a}.btn.no[data-v-2f8b5392]:hover{background-color:#e74f61}", "",{"version":3,"sources":["webpack://./resources/js/frontend/components/social/ReviewButton.vue"],"names":[],"mappings":"AA+IA,sBAMA,iBAAA,CACA,UAAA,CAEA,cAAA,CARA,oBAAA,CAIA,WAAA,CAHA,QAAA,CACA,WAAA,CAKA,iBAAA,CAJA,UAMA,CACA,0BACA,wBAAA,CACA,gBACA,CACA,gCACA,wBACA,CACA,yBACA,wBACA,CACA,+BACA,wBACA","sourcesContent":["<template>\n    <div class=\"d-flex justify-content-center\">\n        <div v-if=\"states === 'loading'\" class=\"spinner-border\" role=\"states\">\n            <span class=\"sr-only\">Loading...</span>\n        </div>\n\n        <div v-else-if=\"states === 'vote' || states === 'voting'\" style=\"position: relative; width: 130px;\">\n            <button type=\"button\"\n                class=\"btn yes\"\n                @click=\"yesVoting()\"\n                :disabled=\"states === 'voting'\">\n                <div v-if=\"voting !== 'yes'\">\n                    通過\n                </div>\n                <div v-else>\n                    <span class=\"spinner-border spinner-border-sm\" role=\"status\" aria-hidden=\"true\"></span>\n                    <span class=\"sr-only\">Loading...</span>\n                </div>\n            </button>\n\n            <button type=\"button\"\n                class=\"btn no\"\n                @click=\"noVoting()\"\n                :disabled=\"states === 'voting'\">\n                <div v-if=\"voting !== 'no'\">\n                    否決\n                </div>\n                <div v-else>\n                    <span class=\"spinner-border spinner-border-sm\" role=\"status\" aria-hidden=\"true\"></span>\n                    <span class=\"sr-only\">Loading...</span>\n                </div>\n            </button>\n        </div>\n\n        <div v-else-if=\"states === 'complete'\">\n            <div class=\"clearfix\">\n                <p class=\"float-left mb-0\" style=\"color: rgb(26, 188, 156);\" v-show=\"voting === 'yes'\">您投了同意票</p>\n                <p class=\"float-right mb-0\" style=\"color: rgb(216, 73, 90);\" v-show=\"voting === 'no'\">您投了反對票</p>\n            </div>\n            <div class=\"progress\" style=\"position: relative; width: 130px; height: 48px;\">\n                <div class=\"progress-bar progress-bar-striped progress-bar-animated\"\n                    role=\"progressbar\"\n                    :style=\"'width: ' + (count.yes / (count.yes + count.no) * 100).toFixed(2) + '%; border-style: solid none solid solid; background-color: rgb(26, 188, 156);'\"\n                    :aria-valuenow=\"count.yes\"\n                    :aria-valuemin=\"0\"\n                    :aria-valuemax=\"count.yes + count.no\">{{ (count.yes / (count.yes + count.no) * 100).toFixed(2) }}%</div>\n                <div class=\"progress-bar progress-bar-striped progress-bar-animated\"\n                    role=\"progressbar\"\n                    :style=\"'width: ' + (count.no / (count.yes + count.no) * 100).toFixed(2) + '%; border-style: solid solid solid none; background-color: rgb(216, 73, 90);'\"\n                    :aria-valuenow=\"count.no\"\n                    :aria-valuemin=\"0\"\n                    :aria-valuemax=\"count.yes + count.no\">{{ (count.no / (count.yes + count.no) * 100).toFixed(2) }}%</div>\n            </div>\n            <div class=\"clearfix\">\n                <p class=\"float-left\" style=\"color: rgb(26, 188, 156);\">{{ count.yes }}</p>\n                <p class=\"float-right\" style=\"color: rgb(216, 73, 90);\">{{ count.no }}</p>\n            </div>\n        </div>\n\n        <div v-else>\n            <h1><span class=\"badge badge-secondary\">?</span></h1>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    name: \"ReviewButton\",\n    props: {\n        cid: {\n            type: Number,\n            required: true,\n        },\n    },\n    data() {\n        return {\n            states: 'loading',\n            voting: null,\n            count: {\n                yes: 0,\n                no: 0,\n            },\n        }\n    },\n    mounted() {\n        let self = this;\n        axios.get(`/api/social/cards/${this.cid}/voted`)\n            .then(function (response) {\n                if (response.data.voted) {\n                    self.count.yes = response.data.count.yes;\n                    self.count.no = response.data.count.no;\n                    self.voting = (response.data.selector) ? 'yes' : 'no';\n                    self.states = 'complete';\n                } else {\n                    self.states = 'vote';\n                }\n            })\n            .catch(function (error) {\n                self.states = 'error';\n                // 無法抓取投票資訊\n                // 需要有個 Error 提示訊息\n            });\n    },\n    methods: {\n        yesVoting() {\n            this.states = 'voting';\n            this.voting = 'yes';\n\n            let self = this;\n            axios.get(`/api/social/cards/${this.cid}/voting/1`)\n                .then(function (response) {\n                    self.count.yes = response.data.count.yes;\n                    self.count.no = response.data.count.no;\n                    self.states = 'complete';\n                })\n                .catch(function (error) {\n                    self.states = 'vote';\n                    // 無法正常投票\n                    // 需要有個 Error 提示訊息\n                });\n        },\n        noVoting() {\n            this.states = 'voting';\n            this.voting = 'no';\n\n            let self = this;\n            axios.get(`/api/social/cards/${this.cid}/voting/0`)\n                .then(function (response) {\n                    self.count.yes = response.data.count.yes;\n                    self.count.no = response.data.count.no;\n                    self.states = 'complete';\n                })\n                .catch(function (error) {\n                    self.states = 'error';\n                    // 無法正常投票\n                    // 需要有個 Error 提示訊息\n                });\n        },\n    },\n};\n</script>\n\n<style scoped>\n    .btn {\n        display: inline-block;\n        margin: 0px;\n        padding: 2px;\n        width: 60px;\n        height: 48px;\n        border-radius: 4px;\n        color: #ffffff;\n        position: relative;\n        cursor: pointer;\n    }\n    .btn.yes {\n        background-color: rgb(26, 188, 156);\n        margin-right: 6px;\n    }\n    .btn.yes:hover {\n        background-color: rgb(29, 200, 166);\n    }\n    .btn.no {\n        background-color: rgb(216, 73, 90);\n    }\n    .btn.no:hover {\n        background-color: rgb(231, 79, 97);\n    }\n</style>\n"],"sourceRoot":""}]);
